import React from 'react';
import { Routes } from 'react-router-dom';
import { Route } from 'react-router';
import loadable from '@loadable/component';

const ProductList = loadable(() => import('@src/product/index'));
const ProductModify = loadable(() => import('@src/product/components/Modify/ProductModify'));
const ProductRegister = loadable(() => import('@src/product/components/Register/ProductRegister'));

const ProductRoutes = () => {
  return (
    <Routes>
      <Route path="/list" element={<ProductList />} />
      <Route path="/register" element={<ProductRegister />} />
      <Route path="/modify/:sid" element={<ProductModify />} />
    </Routes>
  );
};

export default ProductRoutes;
